import userEvent from '@testing-library/user-event'
import React, { useEffect, useRef } from 'react'

function Settings(props) {
  
  const subInput = useRef()

    useEffect(()=>{
        //console.log(props.sub)
    },[])

    return (
    <div className='settings'>
        <div className='closeButton' onClick={()=>props.setShowSettings(false)}>x</div>
        <div>
            <button onClick={()=>props.setShowImages(!props.showImages)} className={"settingsButton "+(props.showImages && "buttonActivated")}>
                Show Images
            </button>
            <input defaultValue={props.sub} ref={ subInput} onChange={()=>{props.setSub(subInput.current.value)}}></input>
            <button onClick={()=>props.fetchData()} className={"settingsButton "}>
                Load From Sub
            </button>
            
        </div>
    </div>
  )
}

export default Settings